import { subjects } from "../data/dummy"
import FeatherIcon from 'feather-icons-react'

const Schedule = () => {
    return (<div className="list-group w-auto">
        {
            subjects.map((item, index) => (
                <a key={index} className="list-group-item list-group-item-action d-flex gap-3 py-3" aria-current="true">
                    <FeatherIcon style={{ backgroundColor: `${item.color}` }} width="32" height="32" className="rounded-circle flex-shrink-0" icon="circle" />
                    <div className="d-flex gap-2 w-100 justify-content-between">
                        <div>
                            <h6 className="mb-0">{item.room}</h6>
                            <p className="mb-0 opacity-75">{item.name}</p>
                            <p className="mb-0" >{item.period}</p>
                        </div>
                        <small><span className={item.task>0 ? "btn btn-light-danger" : "btn"}>{ item.task }</span></small>
                    </div>
                </a>
            )
            )
        }
    </div>)
}

export default Schedule